export const milestones = [0, 1, 2, 3, 4, 5]

export const categoryIds: Set<string> = trackIds.reduce((set, trackId) => {
  set.add(tracks[trackId].category)
  return set
}, new Set())

export const categoryColorScale = d3.scaleOrdinal()
  .domain(categoryIds)
  .range(['#00abc2', '#428af6', '#e1439f', '#9f43e1', '#e54552'])

export const categorySubColorScale = d3.scaleOrdinal()
  .domain(categoryIds)
  .range(['#cef8ff', '#bbd5fc', '#f3b0d7', '#ca90f4', '#f4afb4'])
