// @flow
import React, { Component } from 'react';
import { categoryColorScale, categorySubColorScale, trackIdByCategory, tracks } from '../Snowflake/constants'

import Grid from '@material-ui/core/Grid';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';

class Overview extends Component {
    render() {
        return (
            <Grid container direction="column" spacing={2}>
                {trackIdByCategory.map(tracksByCategory => {
                    return (
                        <Grid item>
                            <Grid container spacing={2}>
                                {tracksByCategory.map(trackId => {
                                    return (
                                        <Grid item xs>
                                            <Table key={trackId} size="small">
                                                <TableBody>
                                                    <TableRow>
                                                        <TableCell colSpan={2} component="th" scope="row" align="center" style={{ background: categoryColorScale(tracks[trackId].category) }}>
                                                            <Typography variant="subtitle1">
                                                                <b>{tracks[trackId].displayName}</b>
                                                            </Typography>
                                                        </TableCell>
                                                    </TableRow>
                                                    <TableRow>
                                                        <TableCell colSpan={2} component="th" scope="row" style={{ background: categoryColorScale(tracks[trackId].category) }}>
                                                            <Typography variant="body2">
                                                                {tracks[trackId].description}
                                                            </Typography>
                                                        </TableCell>
                                                    </TableRow>
                                                    {tracks[trackId].milestones.map((milestone, index) => {
                                                        return (
                                                            <React.Fragment>
                                                                {milestone.summary ? 
                                                                    <TableRow>
                                                                        <TableCell component="th" scope="row" align="center">
                                                                            <b>{index+1}</b>
                                                                        </TableCell>
                                                                        <TableCell component="th" scope="row" style={{ background: categorySubColorScale(tracks[trackId].category) }}>
                                                                            <Typography variant="body2">
                                                                                {milestone.summary}
                                                                            </Typography>
                                                                        </TableCell>
                                                                    </TableRow>
                                                                : null }
                                                            </React.Fragment>
                                                        )
                                                    })} 
                                                </TableBody>
                                            </Table>
                                        </Grid>
                                    )
                                })}   
                            </Grid>
                        </Grid>
                    )
                })}
            </Grid>
        );
    }
}

export default Overview;