// @flow

import type { Milestone, MilestoneMap, TrackId } from './constants'

import KeyboardListener from './common/KeyboardListener'
import NightingaleChart from './common/NightingaleChart'
import React from 'react'
import Track from './common/Track'
import TrackSelector from './common/TrackSelector'
import { trackIds } from './constants'

type SnowflakeAppState = {
  milestoneByTrack: MilestoneMap,
  focusedTrackId: TrackId,
}

const hashToState = (hash: String): ?SnowflakeAppState => {
  if (!hash) return null
  const result = emptyState()
  const hashValues = hash.split('#')[1].split(',')
  if (!hashValues) return null
  trackIds.forEach((trackId, i) => {
    result.milestoneByTrack[trackId] = coerceMilestone(Number(hashValues[i]))
  })
  return result
}

const coerceMilestone = (value: number): Milestone => {
  // HACK I know this is goofy but i'm dealing with flow typing
  switch (value) {
    case 0: return 0
    case 1: return 1
    case 2: return 2
    case 3: return 3
    case 4: return 4
    case 5: return 5
    default: return 0
  }
}

const emptyState = (): SnowflakeAppState => {
  return {
    milestoneByTrack: {
      "TIME_MANAGEMENT": 0,
      "SELF_MANAGEMENT": 0,
      "PROJECT_MANAGEMENT": 0,
      "INITIATIVE": 0,

      "EFFECTIVE_COMMUNICATION": 0,
      "CONFLICT_RESOLUTION": 0,
      "FLEXIBILITY_/ DEALING WITH AMBIGUITY": 0,
      "CREATIVITY_THINKING": 0,

      "MOTIVATING_OTHERS": 0,
      "DEVELOPING_OTHERS": 0,
      "TEAMWORK": 0,
      "ACCOMPLISHMENT": 0,

      "ENGINEERING_WOW PRACTICES": 0,
      "SOFTWARE_FUNDAMENTALS": 0,
      "SOFTWARE_AT SCALE": 0,
      "QUALITY": 0,

      "MOBILE": 0,
      "WEB_CLIENT": 0,
      "SERVER": 0,
      "FOUNDATIONS": 0,
    },
    focusedTrackId: 'MOBILE'
  }
}

const stateToHash = (state: SnowflakeAppState) => {
  if (!state || !state.milestoneByTrack) return null
  const values = trackIds.map(trackId => state.milestoneByTrack[trackId])
  return values.join(',')
}

type Props = {}

class SnowflakeApp extends React.Component<Props, SnowflakeAppState> {
  constructor(props: Props) {
    super(props)
    this.state = emptyState()
  }

  updateHash() {
    const hash = stateToHash(this.state)
    if (hash) window.location.replace(`#${hash}`)
  }

  componentDidMount() {
    const state = hashToState(window.location.hash)
    if (state) {
      this.setState(state)
    } else {
      this.setState(emptyState())
    }
  }

  render() {
    return (
      <main>
        <style jsx global>{`
          .name-input {
            border: none;
            display: block;
            border-bottom: 2px solid #fff;
            font-size: 30px;
            line-height: 40px;
            font-weight: bold;
            width: 380px;
            margin-bottom: 10px;
          }
          .name-input:hover, .name-input:focus {
            border-bottom: 2px solid #ccc;
            outline: 0;
          }
          a {
            color: #888;
            text-decoration: none;
          }
        `}</style>
        <div style={{ display: 'flex' }}>
          <div style={{ flex: 1 }}>
            <h2>Please rate your level of competency on each of the areas below.</h2>
            <p>If you want more information about each of the competencies, visit our <a href="https://docs.google.com/spreadsheets/d/177yMyIl7Oxahb-iPu3nemjhTJ9sLl-t1svFjgoBaXaw/edit?usp=sharing">Competency Matrix in Google Sheets</a>.</p>
            <p>Since itâs impossible to accurately assess a level of competency based on a short sentence the following additional resources are provided:</p>
            <ul>
              <li>A company wide level impact expectation: All levels are somewhat equivalent among competencies. The impact of an individual performance on the company can be used to steer in doubt between two levels.</li>
              <li>A set of behaviours expected of someone having reached that level.</li>
              <li>A set of practices that could (but not necessarily does) demonstrate this level of  competency.</li>
            </ul>
          </div>
          <div style={{ flex: 0 }}>
            <NightingaleChart
              milestoneByTrack={this.state.milestoneByTrack}
              focusedTrackId={this.state.focusedTrackId}
              handleTrackMilestoneChangeFn={(track, milestone) => this.handleTrackMilestoneChange(track, milestone)} />
          </div>
        </div>
        <TrackSelector
          milestoneByTrack={this.state.milestoneByTrack}
          focusedTrackId={this.state.focusedTrackId}
          setFocusedTrackIdFn={this.setFocusedTrackId.bind(this)} />
        <KeyboardListener
          selectNextTrackFn={this.shiftFocusedTrack.bind(this, 1)}
          selectPrevTrackFn={this.shiftFocusedTrack.bind(this, -1)}
          increaseFocusedMilestoneFn={this.shiftFocusedTrackMilestoneByDelta.bind(this, 1)}
          decreaseFocusedMilestoneFn={this.shiftFocusedTrackMilestoneByDelta.bind(this, -1)} />
        <Track
          milestoneByTrack={this.state.milestoneByTrack}
          trackId={this.state.focusedTrackId}
          handleTrackMilestoneChangeFn={(track, milestone) => this.handleTrackMilestoneChange(track, milestone)} />
      </main >
    )
  }

  handleTrackMilestoneChange(trackId: TrackId, milestone: Milestone) {
    const milestoneByTrack = this.state.milestoneByTrack
    milestoneByTrack[trackId] = milestone

    this.updateHash()
    this.setState({ milestoneByTrack, focusedTrackId: trackId })
  }

  shiftFocusedTrack(delta: number) {
    let index = trackIds.indexOf(this.state.focusedTrackId)
    index = (index + delta + trackIds.length) % trackIds.length
    const focusedTrackId = trackIds[index]
    this.setState({ focusedTrackId })
  }

  setFocusedTrackId(trackId: TrackId) {
    let index = trackIds.indexOf(trackId)
    const focusedTrackId = trackIds[index]
    this.setState({ focusedTrackId })
  }

  shiftFocusedTrackMilestoneByDelta(delta: number) {
    let prevMilestone = this.state.milestoneByTrack[this.state.focusedTrackId]
    let milestone = prevMilestone + delta
    if (milestone < 0) milestone = 0
    if (milestone > 5) milestone = 5
    this.handleTrackMilestoneChange(this.state.focusedTrackId, ((milestone: any): Milestone))
  }
}

export default SnowflakeApp
